import React from 'react';

import Behance from './behance/behance';

export default () => {
  return (
    <div className="section">
      <div className="container-fluid">
        <h1>Design Process</h1>
        <p>
          I like to sketch up wireframes, ideas and illustrations using a traditional pencil / sketchbook approach.
          I then build on these ideas with my new favorite tool-suite -- the Apple Pencil + iPad Pro. Using
          Adobe Draw, Sketch and Comps to make low to high fidelity comps and vector work that I then take to high fidelity using Adobe Illustrator, Photoshop and After Effects
          on the Macbook Pro for polish.
        </p>
      </div>
      <Behance />
    </div>
  );
};



